.navbar {
  font-weight: 600;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
}

.nav-item {
  .dropdown-menu {
    border: none;
    margin-top: .5rem;
    border-top: 1px solid #f2f2f2 !important;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.12), 0 2px 4px 0 rgba(0,0,0,0.08);
  }
}

.nav-link {
  .svg-inline--fa {
    font-size: 1.4rem;
  }
}

// custom unick
.has-num-ico {
  position: relative;
  .number-icon{
    position: absolute;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background: red;
    color: white;
    right: 0px;
    .number{
      position: absolute;
      font-size: 10px;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      font-style: normal;
    }
  }
}



